/* Prehistory.css */
.prehistory {
    text-align: center;
    margin: 20px;
  }
  
  .timeline {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .timeline-event {
    margin: 0 10px;
    cursor: pointer;
  }
  
  .slide {
    border: 1px solid #ccc;
    padding: 20px;
    border-radius: 10px;
    background-color: #f9f9f9;
  }
  