body {
    background-color: #f0e68c; /* Light brown background */
  }
  
  .elephant {
    position: relative;
    width: 400px;
    height: 300px;
    margin: 50px auto;
    background-color: #664228; /* Dark brown body */
    border-radius: 50px 50px 0 0; /* Rounded top */
  }
  
  .head {
    position: relative;
    width: 100%;
    height: 150px;
    background-color: #664228;
    border-radius: 50px 50px 0 0;
  }
  
  .ears {
    position: absolute;
    top: 0;
    left: 0;
    width: 100px;
    height: 100px;
    background-color: #664228;
    border-radius: 50px 0 0 50px;
    transform: rotate(-15deg);
  }
  
  .ears:after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 100px;
    height: 100px;
    background-color: #664228;
    border-radius: 50px 0 0 50px;
    transform: rotate(15deg);
  }
  
  .eyes {
    position: absolute;
    top: 50px;
    left: 120px;
    width: 20px;
    height: 20px;
    background-color: black;
    border-radius: 50%;
  }
  
  .eyes:after {
    content: "";
    position: absolute;
    top: 50px;
    right: 120px;
    width: 20px;
    height: 20px;
    background-color: black;
    border-radius: 50%;
  }
  
  .trunk {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 50px;
    height: 100px;
    background-color: #664228;
    border-radius: 50px 50px 0 0;
  }
  
  .body {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 150px;
    background-color: #664228;
  }
  
  .legs {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100px;
  }
  
  .front-legs {
    position: absolute;
    left: 50px;
    width: 100px;
    height: 100px;
  }
  
  .leg1, .leg2 {
    position: absolute;
    bottom: 0;
    width: 30px;
    height: 100px;
    background-color: #664228;
    border-radius: 50px 50px 0 0;
  }
  
  .leg1 {
    left: 0;
  }
  
  .leg2 {
    right: 0;
  }
  
  .back-legs {
    position: absolute;
    right: 50px;
    width: 100px;
    height: 100px;
  }
  
  .leg3, .leg4 {
    position: absolute;
    bottom: 0;
    width: 30px;
    height: 100px;
    background-color: #664228;
    border-radius: 50px 50px 0 0;
  }
  
  .leg3 {
    left: 0;
  }
  
  .leg4 {
    right: 0;
  }