.timeline {
    position: relative;
    width: 100%;
    padding: 20px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .line {
    position: absolute;
    width: 4px;
    background-color: #ccc;
    top: 0;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }
  
  .timeline-event {
    position: relative;
    width: 100%;
    padding: 10px 20px;
    box-sizing: border-box;
    cursor: pointer;
    display: flex;
    align-items: center;
    margin: 10px 0;
    flex-direction: column;
  }
  
  .dot {
    width: 12px;
    height: 12px;
    background-color: #ccc;
    border-radius: 50%;
    margin: 10px 0;
  }
  
  .event-details {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .period {
    font-weight: bold;
  }
  
  .event-name {
    margin-top: 5px;
  }
  
  .popup-slide {
    display: none;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    background-color: #f9f9f9;
    border: 1px solid #ccc;
    padding: 10px;
    border-radius: 5px;
    z-index: 1;
  }
  
  .timeline-event:hover .popup-slide {
    display: block;
  }
  
  /* Media queries - Responsive timeline on screens less than 600px wide */
  @media screen and (max-width: 600px) {
    .timeline::after {
      left: 31px;
    }
  
    .timeline-event {
      width: 100%;
      padding-left: 70px;
      padding-right: 25px;
    }
  
    .timeline-event::before {
      left: 60px;
      border: medium solid white;
      border-width: 10px 10px 10px 0;
      border-color: transparent white transparent transparent;
    }
  
    .timeline-event .dot {
      left: 15px;
    }
  
    .timeline-event.right {
      left: 0%;
    }
  }
  